.header-library::after {
    content: "";
    display: block;   
    width: 100%;
    height: 100%;   
    position: absolute;
    top: 0;     
    left: 0;  
    background: transparent;
    background-image: linear-gradient(to left, #000, #000);
    opacity: 0.56;
}

