.header-library__button{
    display: flex;
    position: absolute;
    top: 0;
    left: 50%;                      
    transform: translate(-50%, 0);
    z-index: 1; 
    margin-top: 114px;

    @media screen and (max-width: 420px) {
        margin-top: 124px;


    }
}


