.header-library__navigation{
    display: flex;
    position: absolute;
    top: 0;
    right: 0;
    margin-right: 32px;
    margin-top: 49px;
    z-index: 1;

    @media screen and (max-width: 420px) {
        margin-right: 20px;
        margin-top: 44px;
    }
}

.navigation-btn{
    text-decoration: none;
    color: #fff;
    cursor: pointer; 
    font-family: roboto;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.16;
}

.header-library__homePage{
    margin-right: 39px;
}

.header-library__libraryPage{
    text-decoration: none;
}

.header-library__libraryPage::after{
    content: "";
    display: block;
    width: 100%;
    height: 3px;
    background-color: #FF6B08;
    margin-left: auto;
}

